.footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 15px;
    height: 20px;
    text-align: center;
    font-size: 14px;
    border-top: 1px solid #e6e6e6;
    color: gray;
}

.footer:before {
    content: '';
    position: absolute;
    z-index: -1;
    right: 0;
    bottom: 0;
    left: 0;
    height: 50px;
    overflow: hidden;
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.2),
    0 8px 0 -3px #f6f6f6,
    0 9px 1px -3px rgba(0, 0, 0, 0.2),
    0 16px 0 -6px #f6f6f6,
    0 17px 2px -6px rgba(0, 0, 0, 0.2);
}

.todo-count {
    text-align: left;
}

.filters {
    display: flex;
    gap: 16px;
}

.filters li {
    display: inline;
    cursor: default;
}

.filters li button {
    display: inline-block;
    color: inherit;
    margin: 3px;
    font: inherit;
    padding: 3px 7px;
    text-decoration: none;
    border: 1px solid transparent;
    border-radius: 3px;
}

.filters li button:not(.selected) {
    cursor: pointer;
}

.filters li:hover button {
    border-color: rgba(175, 47, 47, 0.1);
}

.filters li button.selected {
    border-color: rgba(175, 47, 47, 0.2);
}

.clear-completed {
    font: inherit;
}

.clear-completed:hover {
    text-decoration: underline;
}